:root {
  --color-primary: #6b7a8f;
  --color-secondary: #101118;
  --color-accent: #1d1f2f;
  --color-focus: #6d64f7;
  --base-duration: 600ms;
  --base-ease: cubic-bezier(0.25, 0.46, 0.45, 0.84);
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
  overflow-x: hidden;
}

.visuallyhidden {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  white-space: nowrap;
  width: 1px;
}

.icon {
  fill: #6b7a8f;
  fill: var(--color-primary);
  width: 100%;
}

.btn {
  background-color: #6b7a8f;
  background-color: var(--color-primary);
  border: none;
  border-radius: 0.125rem;
  color: white;
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;
  padding: 1rem 2.5rem 1.125rem;
}
.btn:focus {
  outline-color: #6d64f7;
  outline-color: var(--color-focus);
  outline-offset: 2px;
  outline-style: solid;
  outline-width: 3px;
}
.btn:active {
  transform: translateY(1px);
}

.slider__controls {
  display: flex;
  justify-content: center;
  position: absolute;
  top: 48px;
  width: 100%;
  left: 590px;
}
.slider__controls .btn {
  --size: 64px;
  align-items: center;
  background-color: transparent;
  display: flex;
  height: var(--size);
  width: var(--size);
  margin: 0 8px;
  border: 1px solid #363652;
  padding: 0;
}
.slider__controls .btn:focus {
  border-color: #96ffbc;
  outline: none;
}
.slider__controls .btn--previous > * {
  transform: rotate(180deg);
}
