.slider {
  --slide-size: 70vmin;
  --slide-margin: 4vmin;
  height: 600px;
  margin: 0 auto;
  position: relative;
  /* margin-top: 290px; */
  /* width: var(--slide-size); */
  width: calc(100% - 32px);
}

.slider__wrapper {
  display: flex;
  margin: 0 calc(var(--slide-margin) * -1);
  position: absolute;
  transition: transform 600ms cubic-bezier(0.25, 1, 0.35, 1);
  transition: transform var(--base-duration) cubic-bezier(0.25, 1, 0.35, 1);
}

.slide {
  align-items: center;
  color: white;
  display: flex;
  top: 160px;

  /* -webkit-box-flex: 1;
  flex: 1; */
  flex-direction: column;
  justify-content: center;
  margin: 0 12px;
  opacity: 0.25;
  position: relative;
  text-align: center;
  transition: all calc(600ms / 2) cubic-bezier(0.25, 0.46, 0.45, 0.84),
    transform calc(600ms / 2) cubic-bezier(0.25, 0.46, 0.45, 0.84);
  transition: all calc(var(--base-duration) / 2) var(--base-ease),
    transform calc(var(--base-duration) / 2) var(--base-ease);
  height: 356px !important;
  width: 540px !important;
  z-index: 1;
}
.slide--previous:hover,
.slide--next:hover {
  opacity: 0.5;
}
.slide--previous {
  cursor: w-resize;
  z-index: 9999999;
}
.slide--previous:hover {
  transform: translateX(2%);
}
.slide--next {
  cursor: e-resize;
}
/* .slide--next:hover {
  -webkit-transform: translateX(-2%);
  transform: translateX(-2%);
} */

.slide--current {
  --x: 0;
  --y: 0;
  --d: 50;
  opacity: 1;
  pointer-events: auto;
  -webkit-user-select: auto;
  -moz-user-select: auto;
  -ms-user-select: auto;
  user-select: auto;
  top: 0;
  margin: 0 8px;
  width: 852px !important;
  height: 536px !important;
  position: relative;
  cursor: pointer;
}
