  .slide--current:hover .slide__image-wrapper {
    -webkit-transform: scale(1.025)
      translate(
        calc(var(--x) / var(--d) * 1px),
        calc(var(--y) / var(--d) * 1px)
      );
    transform: scale(1.025)
      translate(
        calc(var(--x) / var(--d) * 1px),
        calc(var(--y) / var(--d) * 1px)
      );
  }
} */

.slide__image-wrapper {
  background-color: #1d1f2f;
  background-color: var(--color-accent);
  /* border-radius: 1%; */
  height: 100%;
  left: 0%;
  overflow: hidden;
  position: absolute;
  top: 0%;
  transition: transform calc(600ms / 4) cubic-bezier(0.25, 0.46, 0.45, 0.84);
  transition: transform calc(var(--base-duration) / 4) var(--base-ease);
  width: 100%;
}

.slide__image {
  --d: 20;
  height: 100%;
  /* left: -5%; */
  /* -o-object-fit: cover; */
  /* object-fit: cover; */
  opacity: 0;
  pointer-events: none;
  /* position: absolute; */
  /* top: -5%; */
  transition: opacity 600ms cubic-bezier(0.25, 0.46, 0.45, 0.84),
    transform 600ms cubic-bezier(0.25, 0.46, 0.45, 0.84);
  transition: opacity var(--base-duration) var(--base-ease),
    transform var(--base-duration) var(--base-ease);
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: 100%;
}
@media (hover: hover) {
  .slide--current .slide__image {
    transform: translate(
      calc(var(--x) / var(--d) * 1px),
      calc(var(--y) / var(--d) * 1px)
    );
  }
}

.slide__headline {
  font-size: 40px;
  font-weight: 600;
  position: relative;
  text-transform: uppercase;
}

.slide__content {
  --d: 60;
  opacity: 0;
  /* padding: 4vmin; */
  position: relative;
  transition: transform 600ms cubic-bezier(0.25, 0.46, 0.45, 0.84);
  transition: transform var(--base-duration) var(--base-ease);
  visibility: hidden;
  width: 651px;
  height: 105px;
  background-color: #0a0a14;
  margin: auto auto 32px auto;
}

.slide__content__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 48px);
  height: 100%;
  margin: auto;
}

.content__container__right {
  display: flex;
}

.content__container__left {
  /* display: flex; */
  text-align: left;
}

.role {
  color: #8989a3;
}

.slide--current .slide__content {
  -webkit-animation: fade-in calc(600ms / 2) cubic-bezier(0.25, 0.46, 0.45, 0.84)
    forwards;
  -webkit-animation: fade-in calc(var(--base-duration) / 2) var(--base-ease)
    forwards;
  animation: fade-in calc(600ms / 2) cubic-bezier(0.25, 0.46, 0.45, 0.84) forwards;
  -webkit-animation: fade-in calc(var(--base-duration) / 2) var(--base-ease) forwards;
          animation: fade-in calc(var(--base-duration) / 2) var(--base-ease) forwards;
  visibility: visible;
}
@media (hover: hover) {
  .slide--current .slide__content {
    transform: translate(
      calc(var(--x) / var(--d) * -1px),
      calc(var(--y) / var(--d) * -1px)
    );
  }
}
.slide__content > * + * {
  margin-top: 2rem;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

